.slick-dots {
  list-style: none;
  padding: 0;
  margin: 0;
  text-align: center; }
  .slick-dots li {
    display: inline-block;
    padding: 0 10px; }
    .slick-dots li button {
      text-indent: -9999px;
      overflow: hidden;
      display: flex;
      justify-content: center;
      align-items: center;
      background: transparent; }
      .slick-dots li button:after {
        content: '';
        display: block;
        height: 4px;
        width: 4px;
        background: #121212;
        border-radius: 4px; }
    .slick-dots li.slick-active button:after {
      content: '';
